import * as React from 'react';


const ProductInfo: React.FC<{}> = () => (
	<div className="product">
		<div className="container">
			<div className="row space-between product__content">
				<div className="product__images">
					<div className="product__image product__image_lg">
						<img src="/image/product.png" width="100%"/>
					</div>

					<div className="row space-between">
						<div className="product__image product__image_sm">
							<img src="/image/product.png" width="100%"/>
						</div>

						<div className="product__image product__image_sm">
							<img src="/image/product.png" width="100%"/>
						</div>

						<div className="product__image product__image_sm">
							<img src="/image/product.png" width="100%"/>
						</div>
					</div>
				</div>

				<div className="product__info">
					<div className="row space-between product__info-head">
						<div className="product__name">Biker hoodie</div>
						<div className="product__price">$24.00</div>
					</div>

					<div className="product__description">
						Lorem ipsum dolor sit amet, consectetur adipisicing elit. Architecto
						aspernatur at consectetur cum cupiditate dolores doloribus exercitationem
						explicabo fuga fugiat ipsum iusto, labore natus
						nisi tempore, tenetur ullam ut veniam!
					</div>

					<div className="row space-between product__facilities">
						<div className="product__size row">
							<p>Size:</p>
							<ul className="product__size-list">
								<li className="product__size-item">XS</li>
								<li className="product__size-item">S</li>
								<li className="product__size-item product__size-item_active">M</li>
								<li className="product__size-item">L</li>
							</ul>
						</div>

						<div className="product__color row">
							<p>Color:</p>
							<div className="goods__color goods__color_sm">
								<div className="goods__color-item goods__color-item_red"/>
								<div className="goods__color-item goods__color-item_green"/>
								<div className="goods__color-item goods__color-item_blue"/>
								<div
									className="goods__color-item goods__color-item_purple
									goods__color-item_active"/>
								<div className="goods__color-item goods__color-item_yellow"/>
								<div className="goods__color-item goods__color-item_black"/>
							</div>
						</div>
					</div>

					<div className="my-pad">
						<div className="order">
							<div className="order__quantity">
								<b className="order__quantity-head">Quantity:</b>

								<span
									className="order__quantity-count"
									contentEditable>1</span>
							</div>

							<div className="order__actions row mt-2">
								<div className="order__like">
									<i className="fas fa-heart"/>
								</div>

								<div className="order__add">Add to cart</div>
							</div>
						</div>
					</div>

					<div className="product__share">
						<b>Share in:</b>
						<i className="fab fa-facebook-f product__share-soc"/>
						<i className="fab fa-twitter product__share-soc"/>
						<i className="fab fa-pinterest-p product__share-soc"/>
					</div>

					<div className="product__categories">
						<b>Categories:</b>
						<span className="ml-1">
							<a className="product__categories-item" href="#">Fashion, &nbsp;</a>
							<a className="product__categories-item" href="#">Men clothes</a>
						</span>
					</div>

					<div className="product__categories">
						<b>Tags:</b>
						<span className="ml-1">
							<a className="product__categories-item" href="#">Fashion, &nbsp;</a>
							<a className="product__categories-item" href="#">Men clothes</a>
						</span>
					</div>

					<div className="product__mark my-pad">
						<b className="product__mark-head">Average mark:</b>
						<span>5.0</span>
					</div>
				</div>
			</div>
		</div>
	</div>
);

export default ProductInfo;
